.formWrapper{display:flex;flex-direction:column;gap:2rem}.formWrapper .form{display:flex;flex-direction:column;gap:1rem}.formWrapper .form input,.formWrapper .form textarea{background:#1a1a1a;border:1px solid #333;color:#fff;padding:.75rem 1rem;border-radius:8px;font-size:1rem;resize:vertical}.formWrapper .form input:focus,.formWrapper .form textarea:focus{outline:none;border-color:#fff}.formWrapper .form button{background:#fff;color:#000;padding:.75rem 1.5rem;border:none;font-weight:600;border-radius:8px;cursor:pointer;transition:background .3s ease}.formWrapper .form button:hover{background:#ccc}.formWrapper .form button:disabled{background:#666;cursor:not-allowed}.formWrapper .actions{display:flex;flex-wrap:wrap;gap:1rem}.formWrapper .actions a{background:#111;border:1px solid #333;color:#fff;padding:.6rem 1rem;border-radius:6px;font-size:.95rem;text-decoration:none;transition:all .2s ease}.formWrapper .actions a:hover{border-color:#fff;background:#222}