.container{color:#fff;min-height:100vh;padding-top:64px;padding-inline:1rem;display:flex;flex-direction:column;gap:3rem;overflow-x:hidden}.header{width:100%}.projects{display:flex;flex-direction:column;align-items:center;gap:3rem;width:100%}.projectCard{display:flex;flex-direction:column;align-items:center;gap:1rem;text-align:center;width:100%}.projectCard video{width:100%;max-width:900px;border-radius:2rem;object-fit:cover}.projectCard img{height:60px;width:auto;margin-top:-2.5rem}.projectCard p{font-family:var(--font-inter);font-size:1.2rem;font-weight:500;color:#fff;max-width:600px}